.loader-wrapper {
  position: absolute;
  top: 100px;
  background-color: #222f3e;
  opacity: 0.3;
  height: 150%;
  width: 100%;
  pointer-events: all;
  z-index: 1000;
  @media (max-width: 960px) {
    top: 40px;
  }
  // @media (max-width: 450px) and (max-height: 380) {
  //   bottom: 0px;
  // }
}

.loader {
  position: relative;
  top: 30vh;
  animation: spinningColor 1.5s ease-in-out infinite;
  margin: 50px auto;
  border: 5px double #f0eff5;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  z-index: 1001;
}

@keyframes spinningColor {
  0% {
    transform: rotate(360deg);
    border-top: 5px dashed #fe6600;
    border-bottom: 5px dashed black;
  }
  25% {
    border-top: 5px dashed #fe6600;
    border-bottom: 5px dashed black;
  }
  50% {
    border-top: 5px dashed #fe6600;
    border-bottom: 5px dashed black;
  }
  75% {
    border-top: 5px dashed #fe6600;
    border-bottom: 5px dashed black;
  }
  100% {
    border-top: 5px dashed #fe6600;
    border-bottom: 5px dashed black;
  }
}
