@keyframes rotate2 {
  // 0% {
  //   transform-origin: center;
  //   transform: rotate(0deg);

  // }
  25% {
    transform-origin: center;
    transform: rotate(-30deg);
  }
  // 75% {
  //   transform-origin: center;
  //   transform: rotate(30deg);

  // }
  75% {
    transform-origin: center;
    transform: rotate(30deg);
  }
}

.myorder-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;

  .myorder-nothing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
    .myorder-nothing-cry {
      background-image: url('https://cdn4.iconfinder.com/data/icons/smileys-for-fun/128/smiley__21-512.png');
      background-size: cover;
      background-position: center top;
      width: 90vw;
      height: 40vh;
      max-width: 300px;
      max-height: 500px;
      margin-bottom: 20px;
      animation: rotate2 3s infinite;
    }
    .myorder-nothing-empty {
      font-size: 3rem;
      font-weight: 800;
      margin-bottom: 20px;
      border-bottom: solid 1px rgba(0, 0, 0, 0.3);
      padding-bottom: 10px;
      color: #ffa502;
    }
    .myorder-nothing-uu {
      font-size: 1.5rem;
    }
  }
}
